@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Raleway";
  color: white;
}
.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav {
  padding: 60px 30px 0px 30px;
  flex: 1.5;
  flex: var(--sidebar-flex);
  background: black;
}
.container {
  padding: 100px;
  background-image: url(/static/media/background.39ead99f.jpeg);
  background-size: cover;
  flex: 6;
  flex: var(--condiv-flex);
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-image: url(/static/media/background.39ead99f.jpeg) contain;
  background-size: cover;
}

.personalpic {
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}

.projectpic {
  border-radius: 15%;
  width: 200px;
  margin: 10px;
}
.backgroundpic {
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}
nav ul {
  font-size: 1.6rem;
  font-size: var(--fontsize-nav);
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li {
  margin: 40px;
}

a {
  text-decoration: none;
  color: white;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
}
p {
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,
h2,
h3,
h4,
p {
  margin: 10px;
}
.contactlogos {
  /* position: absolute; */
  padding-top: 30px;
  padding: 20px;
  /* display: flex; */
  font-size: 50px;
}
.contactlogos i {
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.contactlogos i:hover {
  color: #ffffff;
}
.widecard {
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
}
.widecard:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.active {
  font-weight: bolder;
}
.projects ul li {
  list-style-type: none;
  margin: 30px;
}

.bio {
  max-width: 100px;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }
  .App {
    flex-direction: column;
  }
  .sidebar {
    padding: 30px;
    background: rgb(37, 12, 20);
    background: linear-gradient(90deg, rgba(132, 124, 252, 1) 80%);
  }
  .condiv {
    padding: 50px;
  }
  nav ul {
    display: flex;
  }
  nav ul li {
    margin: 10px;
  }
  .social {
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }
  .social i {
    padding-right: 0px;
  }
}

